import Permission from '../../permission';

export default class WistikiServicePermission extends Permission {
	constructor(acl) {
    super(acl, '/wistikis/:__feathersId', ['GET', 'DELETE', 'PUT', 'POST']);
	}

	build() {
		this.allow('user', ['GET', 'PUT']);
		//this.allow('application', ['GET', 'PUT']);
	}
}
